.library {
  position: fixed;
  top: 0;
  left: 0;
  width: 20rem;
  height: 100%;
  box-shadow: 2px 2px 50px black;
  overflow: scroll;
  transform: translateX(-100%);
  transform: all 0.5s ease;
  opacity: 0;
  background: black;
  h2 {
    padding: 3.25rem;
    color: #a7a7a7;
  }
}

.library-song {
  display: flex;
  align-items: center;
  cursor: pointer;
  padding: 1rem 2rem 1rem 2rem;
  transition: background 0.5s ease;
  img {
    width: 30%;
  }
  &:hover {
    h3 {
      color: white;
    }
    h4 {
      color: #a7a7a7;
    }
  }
}

.song-description {
  padding-left: 1rem;
  h3 {
    font-size: 1rem;
  }
  h4 {
    font-size: 0.7rem;
  }
}

* {
  scrollbar-width: thin;
  scrollbar-color: rgba(155, 155, 155, 0.5) transparent;
}
*::-webkit-scrollbar {
  width: 5px;
}
*::-webkit-scrollbar-track {
  background: transparent;
}
*::-webkit-scrollbar-thumb {
  background-color: rgba(155, 155, 155, 0.5);
  border-radius: 20px;
  border: transparent;
}

.selected {
  background: #171717;
  h3 {
    color: white;
  }
  h4 {
    color: #a7a7a7;
  }
}

.active-library {
  transform: translateX(0%);
  opacity: 1;
}

@media screen and (max-width: 768px) {
  .library {
    width: 100%;
    background: black;
  }
}
